<template>
  <div class="hello">
        <h2> One... </h2>
        <button v-on:click="connect">Connect</button>
  </div>
</template>

<script>
import {connectToFirebase, getFromFirebase} from './firebase-utils.js';
export default {
  name: 'HelloWorld',
  data () {
    return {
      input: '11111'
    }
  },
methods: {
    connect () {
      console.log('connecting');
      connectToFirebase();
      console.log('connected...?');
      getFromFirebase();
      }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



