import firebase from 'firebase';

var connected = false;

export function connectToFirebase() {
    if (!connected) { console.log('connecting to firebase...');
    // Initialize Firebase
    var config = {
        apiKey: "AIzaSyCnzHm9k4aaA-BkM0Y402ZeStCB7-Jx1gE",
        authDomain: "frisking-frenzy.firebaseapp.com",
        databaseURL: "https://frisking-frenzy.firebaseio.com",
        projectId: "frisking-frenzy",
        storageBucket: "frisking-frenzy.appspot.com",
        messagingSenderId: "962102921607"
    };
    firebase.initializeApp(config);
        console.log('initialised firebase');
    connected = true;
}
else {
    console.log('already connected');
}
}

export function getFromFirebase() {
    let ref = firebase.database().ref('subscription-entries');
    ref.on("value", function(snapshot) {
  const values = Object.values(snapshot.val());
  console.log(values);}, function (errorObject) {
    console.log("The read failed: " + errorObject.code);
  });
}



